<template>
<section class="section">
    <div class="container">
        <h3>Reset Password</h3>
        <form action="">
            <div class="field">
                <label for="" class="label">Old Password</label>
                <div class="control">
                    <input v-model="form.oldPassword" type="text" class="input">
                </div>
            </div>
            <div class="field">
                <label for="" class="label">New Password</label>
                <div class="control">
                    <input v-model="form.newPassword" type="text" class="input">
                </div>
            </div>
        </form>
<!--        <p class="notify">Reset Password Successfully</p>-->
        <button @click="changePassword" class="button save">Save</button>
        <button class="button cancel" @click="$router.push({name:'UserProfile'})">Cancel</button>
    </div>
    <app-loading v-if="isLoading"></app-loading>
</section>
</template>

<script>
    import { mapGetters } from 'vuex'
    import appLoading from '../components/Loading'
    export default {
        components: {
          appLoading
        },
        data: () => ({
            isLoading: false,
            form: {
                oldPassword: '',
                newPassword: ''
            }
        }),
        computed: {
          ...mapGetters([
              'auth'
          ])
        },
        methods: {
           async changePassword () {
              try {
                  this.isLoading = true
                  await this.$axios.put('user/change-password', {
                      userId: this.auth._id,
                      oldPassword: this.form.oldPassword,
                      newPassword: this.form.newPassword,
                  })
                  this.$notify({
                      group: 'foo',
                      type: 'success',
                      title: 'ປ່ຽນລະຫັດຜ່ານສໍາເລັດ',
                  })
                  this.$router.push({ name: 'UserProfile' })
                  this.isLoading = false
              } catch (e) {
                  this.isLoading = false
                  throw new Error(e)
              }
            }
        }
    }
</script>

